
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name: "login",
components: {
XInput,
Group,
XButton
},
data(){
return{
loginname:"",
password:"",
nickname:"",
msg:"hello world"
}
},
methods:{
login(){
this.axios.get(`https://musicapi.leanapp.cn/login/cellphone?phone=`+this.loginname+`&password=`+this.password).then((response) => {
console.log(response.data.profile.nickname)
this.nickname = response.data.profile.nickname
})
}
},
beforeCreate:function(){
console.log("beforeCreate: ")
console.log(this.msg)
console.log(document.getElementsByClassName("myp")[0])
},
created:function(){
console.log("created: ")

this.msg+='!!!'
console.log(this.msg)
console.log(document.getElementsByClassName("myp")[0])
},
beforeMount:function(){
console.log("beforeMount: ")

this.msg+='@@@@'
console.log(this.msg)
console.log(document.getElementsByClassName("myp")[0])
},
mounted:function(){
console.log("mounted: ")

console.log(this.msg)
console.log(document.getElementsByClassName("myp")[0]) 
},
beforeUpdate:function(){

console.log('beforeUpdate:重新渲染之前触发')
console.log('然后vue的虚拟dom机制会重新构建虚拟dom与上一次的虚拟dom树利用diff算法进行对比之后重新渲染')
},
updated:function(){

console.log('updated:数据已经更改完成，dom也重新render完成')
},
beforeDestroy:function(){
console.log('beforeDestory:销毁前执行（$destroy方法被调用的时候就会执行）,一般在这里善后:清除计时器、清除非指令绑定的事件等等...')
},
destroyed:function(){
console.log('destroyed:组件的数据绑定、监听...都去掉了,只剩下dom空壳，这里也可以善后')
}
}



