
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

const swiperList = [
{
url:'javascript:;',
img:'static/images/music_swiper01.png',
},{
url:'javascript:;',
img:'static/images/music_swiper02.png'
},{
url:'javascript:;',
img:'static/images/music_swiper03.png'
},{
url:'javascript:;',
img:'static/images/music_swiper04.png'
},{
url:'javascript:;',
img:'static/images/music_swiper05.png'
},{
url:'javascript:;',
img:'static/images/music_swiper06.png'
}]

export default {
components:{
Swiper,
Grid,
GridItem
},
methods:{
swiper_onIndexChange(index){
this.swiper_index = index
},
getPersonalized(){

this.axios.get(`https://musicapi.leanapp.cn/personalized`).then((response) => {
this.personalized = response.data.result;
this.listDetail = "https://musicapi.leanapp.cn/playlist/detail?id=" + response.data.result.id;
})
}
},
created:function(){
this.getPersonalized();
},
data () {
return {
icon:'\ue65a 搜索音乐、视频、歌词、电台',
music_swiper_list:swiperList,
swiper_index:0,
personalized:'',
listDetail:''
}
}
}





