
import $ from 'jquery'
import Card from 'vux/src/components/card/index.vue'

export default {
components: {
Card
},
data(){
return {
icon:'\ue65a 搜索音乐、视频、歌词、电台',
video_list:''
}
},
created:function () {
this.axios.get(`https://musicapi.leanapp.cn/top/mv?limit=10`).then((response) => {
var data = response.data.data;
$(data).each(function (i, d) {
var count = String(d.playCount);
if(count.length > 5){
d.playCount = count.slice(0,-4) + '万';
}
})
this.video_list = data;
});
},
}



